import { showError, showFile } from '../App';
import $ from 'jquery';
import { Table } from 'antd';

const bytesToHuman = (bytes) => {
    if (!bytes) return '0 B';
    const map = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];  // 
                                                                        // ,  undefined
                                                                        // , 
    const i = Math.floor(Math.log(bytes) / Math.log(1024));
    return (bytes / Math.pow(1024, i)).toFixed(2) + ' ' + map[i];
}

const GetById = (id) => {
    var d = {};
    $.ajax({
        url: 'https://files-index.tes286.site/index.json',
        type: 'GET',
        dataType: 'json',
        async: false,
        success: (data) => {
            d = data;
        },
        error: (err) => {
            showError(' (ErrGetFiles)\n' + err);
        }
    });
    if (d === {}) {
        return false;
    }
    const index = d.index
    const path = id.split('/')

    /**
     * Index format:
     * [
     *  {
     *     "name": "File",  // file name
     *     "type": "file",  // file type
     *     "size": "100",   // file size (pre Byte)
     *     "url": "https://files.tes286.site/File"  // file url
     *  },
     *  {
     *    "name": "Folder", // folder name
     *    "type": "folder", // folder type
     *    "children": [     // children
     *     {
     *      "name": "File",
     *      "type": "file",
     *      "size": "100",
     *      "url": "https://files.tes286.site/Folder/File"
     *     }
     *    ]
     *  }
     * ]
     */

    var file = index;
    if (id !== '/') {
        // 
        for (var i = 0; i < path.length; i++) {
            // 
            for (var j = 0; j < file.length; j++) {
                // 
                if (file[j].name === path[i]) {
                    if (file[j].type === 'folder') {
                        file = file[j].children;
                    } else {
                        file = file[j];
                    }
                    // 
                    break;
                }
            }
        }
    } else {
        file = {
            "name": "root",
            "type": "folder",
            "children": index,
            "size": d.total_size
        }
    }
    if (file === index) {
        showError(' (ErrFileNotFound)\n' + id);
        return false;
    }
    showFile(
        <Table dataSource={[file]} columns={[
            {
                title: '',
                dataIndex: 'name',
                key: 'name',
                render: (text, record) => {
                    if (record.url){
                        return <a href={record.url} target='_blank' rel="noreferrer">{text}</a>
                    } else {
                        return text
                    }
                },
                width: '30%'
            },
            {
                title: '',
                dataIndex: 'type',
                key: 'type',
                width: '5%',
                render: (text) => {
                    if (text === 'dir') {
                        return <span></span>
                    } else if (text === 'file') {
                        return <span></span>
                    }
                }
            },
            {
                title: '',
                dataIndex: 'size',
                key: 'size',
                render: text => bytesToHuman(text),
                width: '10%',
            },
            {
                title: '',
                dataIndex: 'url',
                key: 'url',
                render: (text, record) => {
                    if (record.type === 'file') {
                        // Make sure it will open in new tab
                        return <a href={record.url} target="_blank" rel="noreferrer">{text}</a>
                    }
                },
                width: '35%',
            },
        ]} style={{width: '100vh'}} />
    )

};

export { GetById };